.title {
    color: white;
    position: absolute;
    text-align: center;
    justify-self: center;
    align-self: center;
    width: 100%;
    
    
  }
.title h1{
  overflow: hidden;
  display: inline-block;
  font-size: 3rem;
  text-align: center;
  justify-self: center;
  align-self: center;
  margin: 0;
  width: 100%;

  margin-bottom: 10px;
  animation-name: zoomIn;
  animation-duration: 1s;
  
}

@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}



.title p{
  font-size: 1.5em;
  text-align: center;
  justify-self: center;
  align-self: center;
  color: white;
  margin: 0;
  margin-right: 20%;
  margin-left: 20%;
  color: rgb(196, 196, 196);
  animation-name: zoomIn;
  animation-duration: 1.5s;
  
}


@media (max-width: 480px){
  .title {
    width: 100%;
    
  }
.title h1{
  font-size: 3rem;
  margin: 0;
  width: 100%;
  margin-bottom: 10px;
}
.title p{
  font-size: 1rem;
  max-width: 100%;
  margin: 0;
  margin-left: 5%;
  margin-right: 5%;
  
}
  
}