@import './../../utilities/variables';

.project{
  text-align: center;
  justify-self: center;
  align-self: center;
  min-height: 400px;
  width: 100%;
  background-color: $secColour;
}

.projectTitle h2{
  font-size: 2rem;
  margin-bottom: 0;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

.item{
  min-width: 260px;
  min-height: 260px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  outline: none;
}

.titleItem {
  align-self: center;
  
  width: 95%;
  margin: 3px;
  height: 90px;
  display: flex;
  justify-content: center;
}

.titleItem h3{
  align-self: center;
}

.subTitle{
  margin-bottom: 30px;
  margin-top: 0;
  color: red;
}

.projectBox{
  position: relative;
  margin: 10px;
  -webkit-transition: 0.5s ease;
  -moz-transition: 0.5s ease; 
  -o-transition: 0.5s ease;
  overflow: hidden;
  
}
.projectBox:hover{
  box-sizing: border-box;
  background-color: rgb(0, 0, 0);
  cursor: pointer;
}


.itemText{
  display: flex;
  align-self: center;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  transition: 0.5s ease-in-out;
  transition-duration: 0.5s;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  transition: transform .5s;
}

.text{
  color: white;
  font-size: 16px;
  width: 100%;
  min-width: 200px; 
  text-shadow: 1px 1px 1px black;
}

.text p {
  padding-left: 15px;
  padding-right: 15px;
}

