.projectImg {
  width: 100%;
  height: 100%;
  position: relative;
  opacity: 1;
  transition: 0.5s ease;
  backface-visibility: hidden;
}

.projectBox:hover .projectImg {
  opacity: 0.4;
  transform: scale(1.3);
}

.projectBox:hover .itemText {
  opacity: 1;
}

.projectBox:hover .moInfo {
  transition: 0.1s ease;
  opacity: 0;
}

