@import './../../utilities/variables';

.servises{
  display: grid;
  text-align: center;
  justify-self: center;
  align-self: center;
  background-color: $secColour;
  /* height: 100vh; */
}
.servisesTitle{
  height: 40px;
}
.servisesTitle h2{
  font-size: 2em;
  font-weight: bold;
}

.mainPartGlobal{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 40px;
}
.servisesItem{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  
}
.servisesItem .item{
  max-width: 360px;
  flex-direction: column;
  max-height: 200px;
  padding: 15px;
  display: flex;
  align-items: center;
}
.itemFhoto img{
  width: 50%;
}
.item p {
  margin: 0;
}

.buttonForMore {
  
}


.sh-enter{
  opacity: 0;
}
.sh-enter-active{
  opacity: 1;
  transition: opacity 1s;
}
.sh-enter-done{

}
.sh-exit{
  opacity: 1;
}
.sh-exit-active{
  opacity: 0;
  transition: opacity 300ms;
}
.sh-exit-done{
  opacity: 0;
}

.square.entering {
  animation: square 1000ms ease-out forwards;
}

.square.exiting {
  animation: squareExit 400ms ease-in alternate-reverse;
}

.square.exited {
  opacity: 0;
}

@keyframes squareExit {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }

  50% {
    
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes square {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }

  50% {
    opacity: 1;
    transform: translateY(30px);
  }

  100% {
    transform: translateY(0);
  }
}





.btnServises{
  margin-top: 40px;
  margin-bottom: 35px;
  border-style: none;
  border-radius: 5px;
  background: #4676D7;
  color: #fff;
  padding: 10px 16px;
  min-width: 140px;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
}
.btnServises, .btnServises:visited, .btnServises:focus, .btnServises:active{
  outline:0 none !important;
}
.btnServises:hover{
  background: #1d49aa;
}

@media(min-width: 1400px){
  .servisesItem{
    margin-right: 10%;
    margin-left: 10%;
  }
}
@media(max-width: 1080px){
  .servisesItem{
    display: grid;
    grid-template-columns: repeat(3, 2fr);
  }
  .item.mainPart:nth-last-child(-n+2) { /* последние 2 потомка */
    display: none;
  }
}
@media(max-width: 480px){
  .servisesItem{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* margin-top: 0;
    margin-bottom: 0; */
  }
  .item.mainPart:nth-last-child(-n+2) { /* последние 2 потомка */
    display: none;
  }
  .wrapper{
    margin-top: 40px;
    margin-bottom: 30px;
  }
  .buttonForMore {
    /* margin-bottom: 0; */
  }
}